import BookingForm from "../components/BookingForm";
import Testimonials from "../components/Testimonials";
import { motion } from "framer-motion";
import Image from 'next/image';


export default function Home() {
  return (
    <>
      <section className="bg-blue-900 text-white py-20 text-center">
        <motion.h1 initial={{ y:-50, opacity:0 }} animate={{ y:0, opacity:1 }} className="text-5xl font-bold mb-4">
          I’m Perpetua – Your Trusted Immigration Consultant
        </motion.h1>
        <motion.p initial={{ opacity:0 }} animate={{ opacity:1 }} transition={{ delay:0.5 }} className="text-xl mb-6">
          We guide you through every step of your immigration journey, from expert consultation to document preparation and submission.
        </motion.p>
      </section>

      <section id="services" className="py-16 container mx-auto">
        <h2 className="text-3xl font-bold text-center mb-12">Our Services</h2>
        <div className="grid md:grid-cols-3 gap-8">
          {["School Search", "Work Permit", "Permanent Residency", "Visa Services", "Flight Booking"].map((service, idx) => (
            <motion.div key={idx} whileHover={{ scale:1.05 }} className="p-6 bg-white rounded-xl shadow-md text-center">
              <h3 className="text-xl font-semibold mb-2">{service}</h3>
              <p className="text-gray-600">Expert guidance to help you achieve {service.toLowerCase()} success.</p>
            </motion.div>
          ))}
        </div>
      </section>

      <section id="testimonials" className="bg-gray-100 py-16">
        <h2 className="text-3xl font-bold text-center mb-12">Testimonials</h2>
        <Testimonials />
      </section>

      <section id="booking" className="py-16">
        <h2 className="text-3xl font-bold text-center mb-8">Book a Consultation</h2>
        <BookingForm />
      </section>
    </>
  );
}

import Link from "next/link";
import { ArrowRightIcon } from "@heroicons/react/24/solid";

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col bg-brandGray">
      <header className="bg-brandBlue text-white py-6 shadow-md">
        <div className="container mx-auto flex justify-between items-center px-4">
          <h1 className="text-2xl font-bold font-heading">PEPSMILIA</h1>
          <nav className="space-x-4">
            <Link href="/book" className="hover:underline">Book</Link>
            <Link href="/admin/dashboard" className="hover:underline">Admin</Link>
          </nav>
        </div>
      </header>

      <main className="flex-1 flex flex-col justify-center items-center text-center px-4 py-12">
        <div className="max-w-3xl">
          <h2 className="text-5xl font-bold mb-4 font-heading text-brandBlue">
            Simplify Your Service Bookings
          </h2>
          <p className="text-lg text-brandDarkGray mb-6">
            Schedule appointments quickly and manage your services efficiently online.
          </p>
          <Link
            href="/book"
            className="bg-brandLightBlue text-white px-8 py-3 rounded-xl shadow-lg inline-flex items-center hover:bg-brandBlue transition"
          >
            Book Now <ArrowRightIcon className="w-5 h-5 ml-2" />
          </Link>
        </div>

        <div className="mt-10">
          <img
            src="/hero-image.png"
            alt="Booking Illustration"
            className="w-full max-w-xl rounded-lg shadow-md"
          />
        </div>
      </main>

      <footer className="bg-white py-6 text-center shadow-inner">
        &copy; {new Date().getFullYear()} PEPSMILIA. All rights reserved.
      </footer>
    </div>
  );
}

import Image from 'next/image';

export default function Home() {
  return (
    <main className="bg-gray-50 min-h-screen flex flex-col items-center justify-center px-6 md:px-20">
      
      {/* Hero Section */}
      <section className="flex flex-col-reverse md:flex-row items-center justify-between w-full max-w-6xl mt-12 md:mt-24 gap-8">
        
        {/* Text Content */}
        <div className="flex-1 text-center md:text-left">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            Book Your Services Online Effortlessly
          </h1>
          <p className="text-lg md:text-xl text-gray-700 mb-6">
            Schedule appointments, manage bookings, and explore services all in one platform.
          </p>
          <button className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 px-8 rounded-lg transition duration-300">
            Book Now
          </button>
        </div>

        {/* Hero Image */}
        <div className="flex-1 flex justify-center md:justify-end">
          <Image
            src="/hero-image.png"   // make sure your image is in public/hero-image.png
            alt="Online booking illustration"
            width={500}
            height={400}
            className="rounded-lg shadow-lg"
          />
        </div>

      </section>

      {/* Optional: Add a small footer / tagline */}
      <footer className="mt-20 text-gray-500 text-sm">
        © 2026 Pepsmilia. All rights reserved.
      </footer>

    </main>
  );
}
